import { all, takeLatest, call, put, fork } from "redux-saga/effects";
import { getTokenMap, getTokenMapStatus, getTokenPairMap, tokenMapSlice } from './reducer';


import { exchangeAPI } from '../apis/api';
import { getIcon } from '../../utils/swap_calc_utils';
import { CoinInfo, CoinMap } from '@loopring-web/component-lib/static-resource';
import { AddressMap, IdMap, TokenMap } from './interface';

const getTokenMapApi = async ()=>{
    const {tokenSymbolMap: tokens} = (  await exchangeAPI().getTokens())
    let coinMap: CoinMap<any, CoinInfo<any>> = {}
    let tokenMap: typeof tokens = tokens;
    let addressIndex: AddressMap  = {}
    let idIndex: IdMap = {}
    Reflect.ownKeys(tokens).forEach((key: any) => {
        if (!(key as string).startsWith('LP-')) {
            coinMap[ key ] = {
                icon: getIcon(key, tokens),
                name: key,
                simpleName: key,
                description: '',
                company: '',
            }
        }
        addressIndex = {
            ...addressIndex,
            [tokens[key].address]: key
        }
        idIndex = {
            ...idIndex,
            [tokens[key].tokenId]: key
        }
    })
    return {data:{coinMap,addressIndex,idIndex,tokenMap}}
 };
export function* getPostsSaga() {
    try {
        const {data} = yield call(getTokenMapApi);
        yield put(getTokenMapStatus(data));
    } catch (err) {
        yield put(getTokenMapStatus(err));
    }
}
