import { createSlice, PayloadAction, Slice } from '@reduxjs/toolkit'
import { SystemStatus } from './interface';
import { STATUS } from '../constant';

const initialState: Required<SystemStatus> = {
    system: {},
    __timer__: -1,
    status: 'UNSET',
    errorMessage: null,
}
const systemSlice: Slice = createSlice({
    name: 'system',
    initialState,
    reducers: {
        updateSystem(state, action: PayloadAction<Partial<{ chainId: 1 | 5 | 'unknown' }>>) {
            state.status = STATUS.PENDING
        },
        updateRealTimeObj(state, action: PayloadAction<Partial<{ faitPrice: any, gasPrice: number ,forex:number}>>) {
            state.system = {
                ...state.system,
                forex: action.payload.forex? action.payload.forex : state.system.forex,
                faitPrice: action.payload.faitPrice ? action.payload.faitPrice : state.system.faitPrice,
                gasPrice: action.payload.gasPrice ? action.payload.gasPrice : state.system.gasPrice,
            };

        },
        getSystemStatus(state, action: PayloadAction<Partial<SystemStatus>>) {
            // @ts-ignore
            if (action.error) {
                state.status = STATUS.ERROR
                // @ts-ignore
                state.errorMessage = action.error
            }
            state.system = {...state.system, ...action.payload.system};
            state.__timer__ = action.payload.__timer__;
            state.status = STATUS.DONE
        },
        statusUnset: state => {
            state.status = STATUS.UNSET
        }

    },
});
export { systemSlice };
export const {updateSystem, getSystemStatus, statusUnset,updateRealTimeObj} = systemSlice.actions;