import { all, call, fork, put, takeLatest } from "redux-saga/effects"
import { updateSystem, getSystemStatus, updateRealTimeObj } from './reducer'
import { ENV, NETWORK, NETWORKEXTEND } from "./interface"
import store from '../index';
import { reset } from '../account/reducer';
import { ChainId } from 'loopring-sdk';
import { exchangeAPI } from '../apis/api';
import { updateRealTimeAmmMap } from '../Amm/AmmMap';


const getSystemsApi = async <R extends object>(system:any)=> {
    //TODO get some other reuqired id...... put into system

    const { chainId } = system
    const env =  window.location.hostname === 'localhost'? ENV.DEV :ChainId.GORLI === chainId ?  ENV.UAT : ENV.PROD
    const network = ChainId.GORLI === chainId ? ChainId.GORLI: ChainId.MAINNET === chainId ?  ChainId.MAINNET: NETWORKEXTEND.NONETWORK
    const faitPrices = (await exchangeAPI().getFiatPrice({ legal: 'CNY' })).fiatPrices
    const gasPrice = (await exchangeAPI().getGasPrice()).gasPrice / 1e+9
    const baseURL = env === ENV.PROD ? 'https://api3.loopring.io' : 'https://uat2.loopring.io';
    const forex = faitPrices['USDC']
    let {__timer__} = store.getState().system
    __timer__ = (()=>{
        if(__timer__ && __timer__ !== -1){
            clearInterval(__timer__)
        }
        __timer__ = setInterval(async()=>{
            const faitPrices = (await exchangeAPI().getFiatPrice({ legal: 'CNY' })).fiatPrices
            const gasPrice = (await exchangeAPI().getGasPrice()).gasPrice / 1e+9
            const forex = faitPrices['USDC']
            store.dispatch(updateRealTimeObj({faitPrice: faitPrices, gasPrice,forex}))
            store.dispatch(updateRealTimeAmmMap)
        },900000)
        return __timer__//15*60*1000
    })();
    store.dispatch(reset(undefined))
    return { data: { network, env, baseURL, faitPrice: faitPrices, gasPrice, forex} , __timer__ }
}

export function* getUpdateSystem({payload}:any) {
    try {
        // @ts-ignore
        const { system } = payload;
        // console.log('getPostsSaga systemKey',systemKey, systemKeys)
        const { data,__timer__}  = yield call(getSystemsApi, system);

        yield put(getSystemStatus({system:data,__timer__}));
        //TODO check wallect store



    } catch (err) {
        yield put(getSystemStatus(err));
    }
}


function* systemSaga() {
    yield all([takeLatest(updateSystem, getUpdateSystem)]);
}


export const systemForks = [
    fork(systemSaga),
]
 