import { StateBase } from '../interface';
import { ChainId } from 'loopring-sdk';

export enum ENV {
    DEV='DEV',
    UAT='UAT',
    PROD='PROD',
}
export enum NETWORKEXTEND {
    NONETWORK = 'unknown'
}
export type NETWORK = NETWORKEXTEND | ChainId


export type System = {
    env:keyof typeof ENV,
    chainId: 1|5|'unknown'
    network:keyof NETWORK,
    baseURL: string,
    faitPrice:any,
    gasPrice:number,
    forex:number,


}

export type SystemStatus = {
    system:System | {}
    __timer__:number
}  & StateBase



