import { createSlice, PayloadAction, Slice } from '@reduxjs/toolkit'
import {  AmmMapStates } from './interface';
import { STATUS } from '../../constant';
import { AmmPoolStat } from 'loopring-sdk';
import { setAmmState } from './saga';


const initialState:Required<AmmMapStates<object,object>>  = {
    ammMap:{},
    __timer__:-1,
    status:'UNSET',
    errorMessage:null,
}
const ammMapSlice:Slice = createSlice({
    name: 'ammMap',
    initialState,
    reducers: {
        getAmmMap(state, action:PayloadAction<string | undefined>) {
            state.status = STATUS.PENDING
        },
        getAmmMapStatus(state, action: PayloadAction<AmmMapStates<any,any>>) {
            // @ts-ignore
            if (action.error) {
                state.status =  STATUS.ERROR
                // @ts-ignore
                state.errorMessage = action.error
            }
            if(state.ammMap)  {
                state.ammMap = {...action.payload.ammMap};
            }
            state.__timer__  =   action.payload.__timer__ ?  action.payload.__timer__: state.__timer__;
            state.status = STATUS.DONE
        },
        updateRealTimeAmmMap(state, action: PayloadAction<{ammPoolStats?:{[key:string]:AmmPoolStat} }>){
            state.status = STATUS.PENDING
          

        },
        statusUnset: state => {
            state.status = STATUS.UNSET
        }
    },
});
export { ammMapSlice };
export const { getAmmMap, getAmmMapStatus, statusUnset, updateRealTimeAmmMap } = ammMapSlice.actions
