import { all, call, fork, put, takeLatest } from "redux-saga/effects";
import { getAmmMap, getAmmMapStatus, updateRealTimeAmmMap } from './reducer';
import { AmmDetail, PairKey } from '@loopring-web/component-lib/src/static-resource';
import store from '../../index';
import { AmmPoolInfoV3, AmmPoolStat, } from "loopring-sdk";
import { ammpoolAPI } from "stores/apis/api";
import { getTokenPairMap } from '../../token';

type AmmMap<R extends object> ={ [key:string]: AmmDetail<R>} | {}  //key is AMM-XXX-XXX
export const setAmmState = ({ammPoolState}: { ammPoolState: AmmPoolStat }) => {
    const {system: {forex}} = store.getState().system;
    return {
        amountDollar: parseFloat(ammPoolState.liquidityUSD),
        amountYuan: parseFloat(ammPoolState.liquidityUSD) / forex,
        totalLPToken: parseFloat(ammPoolState.lpLiquidity),
        totalA: parseInt(ammPoolState.liquidity[ 0 ]),
        totalB: parseInt(ammPoolState.liquidity[ 1 ]),
        rewardValue: ammPoolState.rewards[ 1 ],
        rewardToken: ammPoolState.rewards[ 0 ],
        feeA: parseInt(ammPoolState.fees[ 0 ]),
        feeB: parseInt(ammPoolState.fees[ 1 ]),
        tradeFloat: {
            percent: '',
            timeUnit: '24h',
            // priceYuan: 100,
            // priceDollar: 1.23123,
            // floatTag: FloatTag.increase,
            // reward: 12312,
            APY: (parseInt(ammPoolState.apyBips) * 1.0 / 100)
        },
        apy: (parseInt(ammPoolState.apyBips) * 1.0 / 100)
    }
}
const getAmmMapApi = async <R extends object>() => {

    let ammMap: AmmMap<R> = {}
    const {ammpools,pairs} = (await ammpoolAPI().getAmmPoolConf());
    const {ammPoolStats} = (await ammpoolAPI().getAmmPoolStats());
    // store.dispatch(getTokenPairMap({tokenPairs:pairs}));

    // const {
    //     tokens,
    //     ammpools,
    //     ammPoolStats,
    // } = await asyncGetTradingInfo()
    let {__timer__} = store.getState().amm.ammMap
    __timer__ = (() => {
        if (__timer__ && __timer__ !== -1) {
            clearInterval(__timer__)
        }
        return setInterval(async () => {
            let ammPoolStats: { [key in keyof R]: AmmPoolStat } = (await ammpoolAPI().getAmmPoolStats()).ammPoolStats as { [key in keyof R]: AmmPoolStat }
            store.dispatch(updateRealTimeAmmMap({ammPoolStats}))
        }, 900000)    //15*60*1000 //900000
    })()

    const {tokenMap: {coinMap, idIndex}} = store.getState();

    // const forex = faitPrice.prices['USDC']


    Reflect.ownKeys(ammpools).forEach(async (key) => {
        const item: AmmPoolInfoV3 = ammpools[ key as string ]
        if (item.market === key) {

            // const tokenA =  ;   //getTokenInfoById(tokens.tokenIdMap, parseInt(item.tokens.pooled[0]))
            // const tokenB =  ; // getTokenInfoById(tokens.tokenIdMap, parseInt(item.tokens.pooled[1]))
            const dataItem: AmmDetail<R> = {
                coinAInfo: coinMap[ idIndex[ item.tokens.pooled[ 0 ] ] ],
                coinBInfo: coinMap[ idIndex[ item.tokens.pooled[ 1 ] ] ],
                feeDollar: 100,  //TODO coinA price * feeA + price * feeB
                feeYuan: 100, //TODO coinA price * feeA + price * feeB  /forex
                isNew: Date.now() - (Number(item.createdAt)) > 14 * 86400 * 1000 ? false : true,//14*24*60*60*1000,
                isActivity: item.status === 7 ? true : false,
                ...setAmmState({ammPoolState: ammPoolStats[ key ]})
            }
            // @ts-ignore
            ammMap[key] = dataItem
        }


    })
    return {ammMap, __timer__}
}


export function* getPostsSaga() {
    try {
        const {ammMap, __timer__} = yield call(getAmmMapApi);
        console.log('--- getPostsSaga:', ammMap)
        yield put(getAmmMapStatus({ammMap, __timer__}));
    } catch (err) {
        yield put(getAmmMapStatus(err));
    }
}

export function* updateRealTimeSaga({payload}: any) {
    try {
        const {ammPoolStats} = payload;
        let {ammMap}:{ammMap:AmmMap<object>} = store.getState().amm.ammMap;
        if (ammPoolStats) {
            Reflect.ownKeys(ammPoolStats).map((key) => {
                // @ts-ignore
                ammMap[ key] = {
                    // @ts-ignore
                    ...ammMap[ key],
                    ...setAmmState({ammPoolState: ammPoolStats[ key as string ]})
                }
            })
        }
        yield put(getAmmMapStatus({ammMap}));
    } catch (err) {
        yield put(getAmmMapStatus(err));
    }
}


export function* ammMapInitSaga() {
    yield all([takeLatest(getAmmMap, getPostsSaga)]);
}

export function* ammMapRealTimeSaga() {
    yield all([takeLatest(updateRealTimeAmmMap, updateRealTimeSaga)]);
}

export const ammMapSaga = [
    fork(ammMapInitSaga),
    fork(ammMapRealTimeSaga),
]

